import { Component, OnInit, Output, EventEmitter, Input, ViewEncapsulation, ChangeDetectionStrategy, ViewChild, ChangeDetectorRef} from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';
import * as moment from 'moment';
import _moment from "moment-timezone";
import { SearchCountryField, CountryISO } from 'ngx-intl-tel-input';
import { CondoAnimations } from 'src/@condo/animations';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-facility-expected-visitor',
  templateUrl: './facility-expected-visitor.component.html',
  styleUrls: ['./facility-expected-visitor.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  animations   : CondoAnimations
})

export class FacilityExpectedVisitorComponent implements OnInit {
  
  @ViewChild('expectedVisitorForm') form:any;
  @Input('popUpInfo') info:any = {};
  @Output() modalResult = new EventEmitter()
  visitor:any = {};
  expectedDurationInfo:string = '';
  visitTypeList:any = [];
  visitCategoryList:any = [];
  isDataSubmitted:boolean = false;

  separateDialCode = true;
  SearchCountryField = SearchCountryField;
  CountryISO = CountryISO;
  preferredCountries: CountryISO[] = [CountryISO.UnitedStates, CountryISO.UnitedKingdom];
  selectedCountryISO:string = CountryISO.Philippines;


  message: any = null;
  timeZone:any;

  constructor(
    private visitorService:VisitorService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private sessionService:SessionService,
    private activateRouter:ActivatedRoute,
    private _changeDetectorRef: ChangeDetectorRef,
  ) { 
  }

  getExpectedVisitorInDate(event) {
    this.visitor.expectedVisitorInTime = event;
    this.expectedDurationChange();
  }

  getExpectedVisitorOutDate(event) {
    this.visitor.expectedVisitorOutTime = event;
    this.expectedDurationChange();
  }

  expectedDurationChange() {
    if(this.visitor.expectedVisitorInTime && this.visitor.expectedVisitorOutTime) {
      let inDate = moment(this.visitor.expectedVisitorInTime);
      let outDate = moment(this.visitor.expectedVisitorOutTime);
      let diffDuration = moment.duration(outDate.diff(inDate));
      this.expectedDurationInfo = diffDuration.days()+" days "+this.isSingleDigit(diffDuration.hours())+":"+ this.isSingleDigit(diffDuration.minutes())+" hours";
      let hours = diffDuration.asHours();
      this.visitor.expectedDuration = Math.round(hours);
    }
  }

  isSingleDigit(digit){
    if(digit <= 0){
      return '00';
    } else {
      return (digit.toString().length > 1) ? digit : '0'+digit;
    }
  }

  resetField() {
    this.visitor={};
    this.visitor.visitTypeId = null;
    this.visitor.visitorCategoryId = null;
    this.expectedDurationInfo='';
  }

  createExpectedVisitor() {

    this.isDataSubmitted = true;

    this.message = null;
    if(!this.form.valid){
      window.scroll({
        top: 0,
        behavior: 'smooth'
      });
      // Show the validation message
      this.message = {
        appearance: 'outline',
        content   : "Fill the Required Fields",
        shake     : true,
        showIcon  : true,
        type      : 'error'
      };
      //Mark for check
      this._changeDetectorRef.markForCheck();
    } else {
      let params = {
        expectedVisitor : {
          ...this.visitor,
          "apartmentUnitId": this.info.apartmentBlockUnitId,
          "apartmentId" : this.sessionService.apartmentId,
          "expectedVisitorPhone": this.visitor.expectedVisitorPhone.number,
          "isActive": true,
          "insertedBy": this.sessionService.userId,
          "insertedOn": _moment().toISOString(),
          "updatedBy": null,
          "updatedOn": null,
          "enteredBy": parseInt(this.sessionService.userId),
          "meetingPersonId": 1,
          "visitorsOrg": "string",
          "entryGateId": null,
          "exitGateId": null,
          "tomeetStaffId": this.visitor.tomeetStaffId,
          "phonecountrycode":  this.visitor.expectedVisitorPhone.countryCode,
        }
      }
      this.visitorService.addExpectedVisitor(params).subscribe((res:any) => {
        this.isDataSubmitted = false;
        if(res.message) {
          this.modalResult.emit({message:'success',id:res.message});
        }
      })
    }
  }

  ngOnInit() {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.visitor.expectedVisitorInTime = moment().add(this.timeZone.offset, 'hours')

    this.resetField();
     //visit type
    let visitParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 15
    }
    this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res:any) => {
      this.visitTypeList = res;
    });
    //Visit Category
    let visitCategoryParams = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 100
    }
    this.lookupService.getLookupValueByLookupTypeId(visitCategoryParams).subscribe((res:any) => {
      this.visitCategoryList = res
    });

    this.selectedCountryISO = this.info.location;
  }
}
