import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { MatDialog } from '@angular/material/dialog';
import { FacilityModalComponent } from '../facility-modal/facility-modal.component';

import interactionPlugin from "@fullcalendar/interaction";
import resourceTimelinePlugin from "@fullcalendar/resource-timeline";

import * as moment from 'moment';
import _moment from "moment-timezone";

import resourceTimeline from "@fullcalendar/resource-timeline";

@Component({
  selector: 'app-facility-calender',
  templateUrl: './facility-calender.component.html',
  styleUrls: ['./facility-calender.component.scss']
})
export class FacilityCalenderComponent implements OnInit {
  plugins:any = [ interactionPlugin, resourceTimelinePlugin ];
  header:any = {
    left: 'prev,next',
    center: 'title',
    right: 'resourceTimelineDay,resourceTimelineWeek,resourceTimelineMonth'
  };
  initialView:string = 'resourceTimelineDay';
  calendarResult:any = [];
  rooms:any = [];
  schedulerLicenseKey:string = "CC-Attribution-NonCommercial-NoDerivatives";

  @Input('popUpInfo') info:any = {};
  loadingData = false;

  timeZone:any;

  constructor(private facilityService:FacilityService,private sessionService:SessionService,
    private sharedService:SharedService,
    public dialog: MatDialog,) {}
  
  addEvent(calendarInfo) {
    if(!this.info.facilityId) {
      const dialogRef = this.dialog.open(FacilityModalComponent, {
        panelClass: 'material-dialog-big',
        height:'inherit',
        width:'auto',
        data: {
          type:'facility-booking',
          info: {
            type : 'create',
            dataFrom:'calendar',
            calendarData: calendarInfo
          }
        }
      });
      dialogRef.afterClosed().subscribe(result => {
        if(result) {
          this.facilityBookingList()
        }
      });
    }
  }

  dateEventClick(info) {
    if(!this.info.facilityId) {
      let id = info.event.extendedProps.apartmentFacilityBookingId
      const dialogRef = this.dialog.open(FacilityModalComponent, {
        panelClass: 'material-dialog-big',
        height:'inherit',
        width:'auto',
        data: {
          type:'facility-booking',
          info: {
            type : 'view',
            dataFrom:'calendar',
            bookingId:id
          }
        }
      });
      dialogRef.afterClosed().subscribe(result => {
        if(result) {
          this.facilityBookingList()
        }
      });
    }
  }

  facilityBookingList() {
    let bookingParams = {
      apartmentId: this.sessionService.apartmentId,
      facilityBookingStatusID: 188, //ApprovedId
    }
    this.facilityService.getApartmentFacilityBookingsByApartmentId(bookingParams).subscribe((res:any) => {
      if(res.length > 0) {
        if(this.info.facilityId) {  //popup
          for(let data of res) {
            if(this.info.facilityId == data.apartmentFacilityId) {
              data.resourceId = data.apartmentFacilityId;
              data.title = data.eventName;
              data.start =  `${moment(data.bookedForDate).format('YYYY-MM-DD')} ${data.bookedFromTime}`;
              data.end =  `${moment(data.bookedForDate).format('YYYY-MM-DD')} ${data.bookedToTime}`;
              data.color = 'red';
              break;
            } 
          }
        } else {                       //router
          res.forEach((data)=>{
            data.resourceId = data.apartmentFacilityId;
            data.title = data.eventName;
            data.start =  `${moment(data.bookedForDate).format('YYYY-MM-DD')} ${data.bookedFromTime}`;
            data.end =  `${moment(data.bookedForDate).format('YYYY-MM-DD')} ${data.bookedToTime}`;
            data.color = 'red';
          })
        }
        this.calendarResult = res;
      }
      this.loadingData = true;
    })
  }

  ngOnInit(){

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    //Facility List
    let categoryParams = {
      apartmentId: this.sessionService.apartmentId
    }
    this.facilityService.getApartmentFacilitiesByApartmentId(categoryParams).subscribe((res:any) => {
      if(res.length > 0) {
        let rooms = [];
        if(this.info.facilityId) {  //popup
          for(let facility of res) {
            if(this.info.facilityId == facility.apartmentFacilityId) {
              rooms.push({id:facility.apartmentFacilityId,title:facility.facilityName});
              break;
            }
          }
        } else {                        //router
          res.forEach((data)=>{
            rooms.push({id:data.apartmentFacilityId,title:data.facilityName});
          })
        }
        this.rooms = rooms;
      }
    })
    this.facilityBookingList();
  }
}
