import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FacilityCreateBookingComponent } from './facility-create-booking/facility-create-booking.component';
import { FacilityCalenderComponent } from './facility-calender/facility-calender.component';
import { UserFacilityBookingListComponent } from './user-facility-booking-list/user-facility-booking-list.component';
import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

const routes: Routes = [
	{ path: '', redirectTo: 'create-booking', pathMatch: 'full'},
  	{ path: 'create-booking', component: FacilityCreateBookingComponent, canActivate: [AuthGuard]},
	{ path: 'edit-booking/:id', component: FacilityCreateBookingComponent, canActivate: [AuthGuard]},
	{ path: 'view-booking/:id', component: FacilityCreateBookingComponent, canActivate: [AuthGuard]},
	{ path: 'view-facility-calendar', component: FacilityCalenderComponent, canActivate: [AuthGuard]},
	{ path: 'all-bookings', component: UserFacilityBookingListComponent, canActivate: [AuthGuard]},
	{ path: '**', redirectTo: 'create-booking', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FacilityCreateBookingRoutingModule { }
